// commentId : id ()
//  id threadId,/comment?t=1&type=6&threadId=A_EV_2_6559519868_32953014&content=test
export const commentApi = (args: {
	type?: number;
	t?: number;
	id?: string | number;
	content: string;
	commentId?: string | number;
	threadId?: string;
}) => {
	const { type = 6, t = 1 } = args;
	return axiosIns.get("/comment", { params: { type, t, ...args, timeStamp: Date.now() } });
};
